@use '../helpers/' as *;

.microsoft {
  &__body {
    width: 88%;
    margin: 0 auto;
    
    @include laptop {
      width: 100%;
    }
  }
    
  &__questions {
    margin-bottom: fluid(100, 60);
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: rem(100);
    
    @include laptop {
      column-gap: fluid(60, 40);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: fluid(60, 40);
    }
  }

  &__article {
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);
  }

  &__preview-description {
    margin-bottom: 0;
  }

  &__preview-button {
    margin-right: auto;
  }

  &__accordion-list {
    display: grid;
    row-gap: fluid(40, 30);

    @include mobile {
      row-gap: fluid(20, 16);
    }

    &--has-counter {
      --accordionListCounterHeight: #{fluid(52, 44)};
      counter-reset: custom-counter;
    
      .microsoft__accordion-item {
        display: flex;
        column-gap: fluid(20, 16);
        counter-increment: custom-counter;
        &::before {
          @include flex-center;
          content: counter(custom-counter, decimal-leading-zero);
          min-width: #{fluid(54, 46)};
          height: var(--accordionListCounterHeight);
          padding-top: rem(3);
          background-color: var(--background-additional);
          @include fluid-text(20, 18);
          font-weight: 700;
          border-radius: var(--radius-small);
          border: var(--border);
        }
      }
    }
  }
  &__accordion-item {
    padding-block: 1rem;
    &:not(:last-child) {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;

        height: rem(2);
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }
  }

  &__slider {
    width: 100%;
    display: flex;
    flex-direction: column;

    &-top {
      margin-bottom: fluid(60, 40);
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-title {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
      row-gap: rem(20);
    }

    &-container {
      display: flex;
      flex-direction: column;
      row-gap: rem(20);
    }

    &-content {
      padding-inline: fluid(30, 10);
      padding-block: fluid(30, 20);
      display: flex;
      // flex-direction: row;
      column-gap: rem(100);
      background-color: var(--background-additional);
      border: var(--border);
      border-radius: var(--radius);
      transition: var(--transition-with-border);
      user-select: none;

      @include hover {
        border-color: var(--border-hover);
        background-color: var(--background-basic);
      }

      @include laptop {
        column-gap: fluid(60, 40);
      }

      @include mobile {
        flex-direction: column;
        // justify-content: space-between;
        row-gap: fluid(60, 40);
        column-gap: 0;
      }
    }

    &-nav {
      flex: 1;               
      max-width: 33.33%;        
      display: flex;
      flex-direction: column;
      // align-items: flex-start;
      row-gap: fluid(40, 20);
      // gap: fluid(40, 20);
      opacity: 0;
      transform: translateY(rem(15));
      transition: opacity 0.8s ease, transform 0.8s ease;
      will-change: opacity, transform;

      &.visible {
        opacity: 1;
        transform: translateY(0);
      }

      @include mobile {
        max-width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: rem(40);
      }
    }

    &-item {
      display: flex;
      align-items: center;
      gap: rem(4);
      cursor: pointer;
      
      &--active {
        pointer-events: none;
        .microsoft__slider-dot {
          background-color: var(--elements-accent);
          transform: scale(1.5);
        }
        .microsoft__slider-btn {
          color: var(--text);
          transform: scale(1.05);
          opacity: 1;
        }
      }

      @include hover {
        .microsoft__slider-dot {
          background-color: var(--elements-accent);
          transform: scale(1.5);
        }
        .microsoft__slider-btn {
          color: var(--text-basic); 
          transform: translateX(1rem) scale(1.2); 
          opacity: 1;
        }
      }
    }

    &-dot {
        @include square(rem(12));
        border-radius: 50%;
        background-color: var(--text-additional);
        margin-right: fluid(20, 10);
        transition: background-color 0.3s ease-in-out, transform 0.5s ease-in-out;
        will-change: transform;
      }

      &-btn {
        transition: color 0.3s ease-in-out,transform 0.5s ease-in-out;
        color: var(--text-additional);
        will-change: transform;
      }
  }

  &__screens {
    flex: 2;               
    max-width: 66.66%;        
    display: flex;
    overflow: hidden;
    position: relative;
    backface-visibility: hidden;
    perspective: 1000;
    
    @include mobile {
      max-width: 100%;
      flex-direction: column;
    }

    .swiper-wrapper {
      display: flex;
    }

    .swiper-slide {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      row-gap: fluid(40, 20);
      height: auto !important;
      transition: opacity 0.6s ease, transform 0.6s ease;
      will-change: opacity, transform;
      pointer-events: auto;  

      .microsoft__screens-description,
      .microsoft__screens-image {
          opacity: 1;
          transform: translateY(0);
          transition: transform 0.6s ease, opacity 0.6s ease;
      }
    }

    &-description {
      opacity: 0;
      transform: translateY(rem(15));
      transition: opacity 0.8s ease, transform 0.8s ease;
    }

    &-image {
      padding-top: fluid(30, 20);;
      position: relative;
      margin-top: auto;
      opacity: 0;
      transform: translateY(rem(20));
      transition: opacity 0.8s ease, transform 0.8s ease;

      &::after {
        content: '';
        width: 100%;
        height: rem(2);
        position: absolute;
        top: 0;
        left: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }

      img {
        width: 100%;
        max-width: rem(580);
        height: auto;
        border-radius: var(--radius);
        object-fit: contain;
      }
    }
  }
}