@use '../helpers/' as *;

.clients {
  &__body {
    width: 88%;
    margin: 0 auto;
    // margin-bottom: fluid(60, 40);
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);

    @include laptop {
      width: 100%;
    }

    @include mobile {
      row-gap: fluid(60, 40);
    }
  }

  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: rem(100);
    margin-bottom: rem(60);

    @include laptop {
      // grid-template-columns: 1fr 1fr;
      column-gap: fluid(60, 40);
      margin-bottom: rem(20);
    }

    @include mobile {
      grid-template-columns: 1fr;
      grid-template-columns: 100%;
      row-gap: fluid(40, 20);
      margin-bottom: 0;
    }
  }

  &__text-slider {
    &-wrapper {
      width: 100%;
      display: flex;
      position: relative;
      overflow: hidden;
    }

    &-item {
      display: flex;
      flex-direction: column;
      row-gap: fluid(40, 20);
      transition: opacity 1s ease-in-out;
    }
  }

  &__slider {
    height: var(--slider-height);
    position: relative;
    overflow: hidden;

    @include mobile {
      height: var(--slider-height-mobile);
    }
  }

  &__video {
    display: flex; 
    align-items: center; 
    justify-content: center; 
    position: absolute; 
    height: 100%; 
    width: auto; 
    top: 50%; 
    left: 50%; 
    transform: translate(-50%,-50%); 
    z-index : -1; 
    opacity: 0.9; 
    mix-blend-mode: lighten;
    object-fit: cover;           // масштабирование видео
    -webkit-playsinline: true;   // старые iOS
    playsinline: true;            // новые iOS
    
    &::-webkit-media-controls-start-playback-button { 
      display: none!important; 
      -webkit-appearance: none; 
    }
  }

  &__slider-list {
    margin-top: calc(var(--slider-height) / 2 - var(--slider-item-height) / 2);
    text-align: center;
    transition: var(--slide-transition);

    @include mobile {
      margin-top: calc(var(--slider-height-mobile) / 2 - var(--slider-item-height-mobile) / 2);
    }
  }

  &__slider-item {
    position: relative;
    height: var(--slider-item-height);
    text-align: center;
    opacity: 0.4;
	  transition: opacity 0.5s var(--slide-transition), transform 2s var(--slide-transition);
    

    &.swiper-slide-next,
    &.swiper-slide-prev {
      transform: scale(0.4);
    }

    &.swiper-slide-active {
      opacity: 1;
      transform: scale(1);
      cursor: pointer;
    }

    @include mobile {
      height: var(--slider-item-height-mobile);
    }
  }

  &__slider-image-wrapper > * {
	  position: absolute;
	  width: 100%;
	  height: 100%;
	  perspective: 150px;
	  transform-style: preserve-3d;
  }

  &__slider-image {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    
    &--asml {
      background-image: url('../img/logos/asml.png')
    }

    &--iwc {
      background-image: url('../img/logos/iwc.png')
    }

    &--bb {
      background-image: url('../img/logos/bb.png')
    }
  }

  &__slider-image-left {
    perspective-origin: left center;
    clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%); 
    .clients__slider-image {
      animation: image-left 1s ease-in-out infinite;
      animation-direction: alternate;
    }
  }

  &__slider-image-right {
    perspective-origin: right center;
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
    margin-left: -.55px;
    // Фикс вертикальной полосы на некоторых дисплеях
    .clients__slider-image {
      animation: image-right 1s ease-in-out infinite;
      animation-direction: alternate;
    }
  }

  &__slider-controls {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    z-index: 2;
  }
  
  &__facts {
    width: 100%;
    margin-bottom: rem(60);

    @include laptop {
      margin-bottom: rem(20);
    }

    @include mobile {
      margin-bottom: 0;
    }
  }

  &__reviews {
    width: 100%;
    display: flex;
    flex-direction: column;
  } 
}



