@use '../helpers/' as *;

.reviews {
  &__top {
    margin-bottom: fluid(60, 40);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: fluid(20, 10);
    row-gap: rem(20);
  }

  &__content {
    width: 80%;
    margin: 0 auto;
    padding-inline: fluid(30, 10);
    padding-block: fluid(30, 20);
    display: flex;
    position: relative;
    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius);
    transition: var(--transition-with-border);

    &::after {
      content: '';
      position: absolute;
      top: fluid(24, 20);
      left: fluid(30, 10);
      background-image: url('../img/quotes.svg');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      @include square(fluid(64, 36));
    }

    @include laptop {
      width: 100%;
    }
  }

  &__slider-wrapper {
    width: 100%;
    padding-left: fluid(94, 56);
    padding-right: fluid(30, 20);
    display: flex;
    position: relative;
    overflow: hidden;
    transition: transform var(--transition-duration) ease-in-out, height var(--transition-duration) ease-in-out;
    will-change: transform, height;

    @include mobile {
      padding-left: 0;
      margin-top: rem(50);
    }

    .swiper-scrollbar-vertical, .swiper-scrollbar {
      position: absolute;
      right: 0;
      bottom: 0;
      top: 0;
      width: 4px;
      background-color: var(--elements-accent-pale-more);
      border-radius: 2px;
      z-index: 1;
    }

    .swiper-scrollbar-drag {
      background-color: var(--elements-accent);
      border-radius: 2px;
      z-index: 1;
    }

    @media (width <= rem(539)) {
      width: 100%;
      margin-left: 0;
    }
  }

  &__slider-list {
    display: flex;
    
  }

  &__slider-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    row-gap: fluid(40, 20);
    position: relative;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;

    &.swiper-slide-active {
      opacity: 1;
    }
  }

  &__author {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-end;

    &-position {
      color: var(--text-additional);
      font-size: 14px;
    }
  }

  &__star {
    position: absolute;
    @include square(rem(24));
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 0;
    transform: translateY(0) rotate(0deg);
    animation: starFly var(--duration, 2s) ease-out forwards;
    animation-delay: var(--delay, 0ms);
    will-change: transform, opacity;
    z-index: 2;
    pointer-events: none;
  }
}
